@charset "utf-8";

// Define defaults for each variable.

$base-font-family: "Lato", sans-serif !default;
$base-font-size:   1.2rem !default;
$base-font-weight: 400 !default;
$small-font-size:  $base-font-size * 0.875 !default;
$base-line-height: 1.5 !default;

$title-font-family: "Nunito", sans-serif !default;

$spacing-unit:     1em !default;

$text-color:       #1c3144 !default;
$background-color: #ffffff !default;
$brand-color:      #d14242 !default;

$white-color:      #ffffff !default;

$grey-color:       #595959 !default;
$grey-color-light: lighten($grey-color, 10%) !default;
$grey-color-dark:  darken($grey-color, 25%) !default;

$table-text-align: left !default;

// Width of the content area
$header-width:    1024px !default;
$content-width:    800px !default;

$on-palm:          600px !default;
$on-laptop:        800px !default;

// Use media queries like this:
// @include media-query($on-palm) {
//   .wrapper {
//     padding-right: $spacing-unit / 2;
//     padding-left: $spacing-unit / 2;
//   }
// }
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}

@mixin relative-font-size($ratio) {
  font-size: $base-font-size * $ratio;
}

// Import partials.
@import
  "font-faces",
  "minima/base",
  "minima/layout",
  "minima/syntax-highlighting"
;
