/**
 * Site header
 */
.site-header {
  display: block;
  width: 100%;
  height: $spacing-unit * 6;
  position: fixed;
  background-image: url('/assets/header-background.webp');
  color: $white-color;

  // Positioning context for the mobile navigation icon
  position: relative;

  .wrapper {
    display: flex;
    max-width: $header-width;
    flex-direction: row;
    align-items: center;
    margin: auto;
    padding: 0;
  }

  @include media-query($header-width) {
    display: flex;
    height: 100vh;
    position: static;
    left: 0;
    top: 0;

    .wrapper {
      flex-direction: column;
    }
  }
}

.site-title {
  height: $spacing-unit * 5;
  width:  $spacing-unit * 5;
  margin:  $spacing-unit / 2;

  &:active {
    color: $brand-color;
  }

  &:visited {
    color: inherit;
  }
}

.site-nav {
  display: flex;
  align-items: center;
  flex-direction: row;

  .menu-icon {
    display: none;
  }

  .page-link {
    color: $white-color;
    margin: 2rem 1rem;
    border: 0 solid $brand-color;
    box-sizing: border-box;
    padding: 0 0.5rem;
    transition: color 0.3s ease-in-out, border 0.2s ease-in-out;

    &:hover {
      border-bottom: 3px solid $brand-color;
      text-decoration: none;
    }

    &:active {
      border-bottom: 3px solid darken($brand-color, 15%);
    }
  }

  @include media-query($on-palm) {
    flex-direction: column;
  }
}



/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $grey-color-light;
  padding: $spacing-unit 0;
}

.footer-heading {
  @include relative-font-size(1.125);
  margin-bottom: $spacing-unit / 2;
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.social-media-list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  @include relative-font-size(1.2);

  & > li > a {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}

.footer-col-wrapper {
  @include relative-font-size(0.9375);
  color: $grey-color;
  margin-left: -$spacing-unit / 2;
  @extend %clearfix;
}

.footer-col {
  float: left;
  margin-bottom: $spacing-unit / 2;
  padding-left: $spacing-unit / 2;
}

.footer-col-1 {
  width: -webkit-calc(100% - (#{$spacing-unit} / 2));
  width:         calc(100% - (#{$spacing-unit} / 2));
}

.footer-col-2 {
  width: -webkit-calc(100% - (#{$spacing-unit} / 2));
  width:         calc(100% - (#{$spacing-unit} / 2));
}



/**
 * Page content
 */
.page-content {
  padding: $spacing-unit 0;
  flex: 1;
}

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: $spacing-unit;
  }
}

.post-meta {
  font-size: $small-font-size;
  color: $grey-color;
}

.post-link {
  display: block;
  text-align: left;
  @include relative-font-size(1.5);
}



/**
 * Posts
 */
.post-header {
  margin-bottom: $spacing-unit;
}

.post-title {
  @include relative-font-size(2.625);
  letter-spacing: -1px;
  line-height: 1;

  @include media-query($on-laptop) {
    @include relative-font-size(2.25);
  }
}

.post-content {
  margin-bottom: $spacing-unit;

  h2 {
    @include relative-font-size(2);

    @include media-query($on-laptop) {
      @include relative-font-size(1.75);
    }
  }

  h3 {
    @include relative-font-size(1.625);

    @include media-query($on-laptop) {
      @include relative-font-size(1.375);
    }
  }

  h4 {
    @include relative-font-size(1.25);

    @include media-query($on-laptop) {
      @include relative-font-size(1.125);
    }
  }
}

.home, article {
  padding: 4rem 0;
}
